import React from 'react';
import { Divider, Card} from 'antd';
import '../App.css';
import  BoardHubImg  from "./boardhub.png";
import CollectPopImg from "./collectpop.png";

export function Part2() {

  return (

      <div className='Part2-first-container'>
          <div className="project-name-container">
              <label className="project-name">ProJect</label>
          </div>
          <section className="project-content-container">
              <div className="project-title">
                  <label className="project-title-name">
                      Project-BoardHub
                  </label>
              </div>
              <Divider />
              <div className="project-content">
                  <Card
                      className='Project-card'
                      cover={<img alt="프로필사진" src={BoardHubImg} />}
                      bodyStyle={{ padding: 0 }}
                  />
                  <div className="Card-About">
                      <label className="Card-About-content">
                          통합 보드게임 커뮤니티 서비스
                      </label>
                      <br/><br/>
                      <label className="Card-About-content-second">
                          온라인 게임 같은 경우 활성화 되어 있지만 보드게임 같은 경우 활성화<br/>
                          되어있지 않다고 생각하여 남녀노소 연령대 관계 없이 모두 즐길수 있도록 <br/>
                          활성화 시키자라는 목표로 개발한 프로젝트.
                      </label><br/><br/>
                      <a className="Card-About-content-second-thrid"
                         href="https://github.com/CodeName4885/BoardHub"
                         target="_blank"
                         rel="noopener noreferrer"
                      >Git 이동</a>
                  </div>
              </div>
          </section>

          <section className="project-content-container-2">
              <div className="project-title">
                  <label className="project-title-name">
                      Project-CollectPop
                  </label>
              </div>
              <Divider />
              <div className="project-content">
                  <Card
                      className='Project-card-second'
                      cover={<img alt="프로필사진" src={CollectPopImg} />}
                      bodyStyle={{ padding: 0 }}
                  />
                  <div className="Card-About">
                      <label className="Card-About-content">
                          팝업스토어 통합 커뮤니티 서비스
                      </label>
                      <br/><br/>
                      <label className="Card-About-content-second">
                          최근 핫한 팝업스토어에 대해서 찾아볼때에<br/>
                          한눈에 알아볼 수 있는 사이트가 없기에 구상하였고,<br/>
                          지도, 길찾기를 사용하여 따로 검색하지 않아도 바로 <br/>
                          볼 수 있고 한눈에 알아볼 수 있도록 만드는것을 목표로 둔 프로젝트.
                      </label><br/><br/>
                      <a
                          className="Card-About-content-second-thrid"
                          href="https://github.com/CodeName4885/project_collectpop"
                          target="_blank"
                          rel="noopener noreferrer"
                      >
                          Git 이동
                      </a>
                  </div>
              </div>
          </section>
      </div>

  );
}
