import React, { useState } from 'react';
import { Card, Divider } from 'antd';
import '../App.css';
import Profile from './profile.jpg';

const { Meta } = Card;

const IntroductionSection = ({ onToggle }) => (
    <section className='second-card-container'>
        <div className="About-name-second-container">
        <label className="About-name-second"> Introduction</label>
        <label className='About-name' onClick={onToggle}>
            About Me
        </label>
        </div>
        <Divider />
        <div className="About-title">
            <label className="About-title-name"> 반갑습니다! <br />Java 백엔드 신입 개발자 <br /> 김태욱입니다.</label>
            <Divider />
            <br />항상 긍정적이고 활기차면서도 포기하지 않고<br />
            끊임없이 도전하는 개발자가 되고싶습니다!<br />
        </div>
    </section>
);

const AboutMeSection = ({ onToggle }) => (
    <section className='second-card-container'>
        <div className="About-name-second-container">
            <label className="About-name" onClick={onToggle}> Introduction</label>
                <label className='About-name-second'>
                    About Me
                </label>
        </div>
        <Divider />
        <div className="About-title">
            <label className="About-title-second-name">" 포기하지 않고 지치지 않는 개발자 "</label><br/><br/>
            <label className="content-title">패기와 열정이 넘치는 24살 개발자 김태욱입니다!</label>
            <Divider />
            <label className="content-title-second">
            평소에 컴퓨터에 흥미가 있던 저는 개발에 발을 들이게 되었고 <br/> 국비지원 과정을 통해 백엔드 개발에 대한 공부를 하면서<br/>
            웹 서비스 과정에 대해 재미를 느끼게 되었습니다.<br/>
            개발을 하는 과정에 동고동락하며 매일매일 개발 공부에 모두 투자하며<br/>
            다양한 기술들을 익혀나가고 있습니다.<br/>
            </label>
            <article id="contact" className="contact-wrap">
                <h1 className="contact-font">contact</h1>
                <div>
                    <ul className="contact-list">
                        <li>Phone: 010-8880-9978</li>
                        <li>Email: xodnrdlwy@naver.com</li>
                        <li>GitHub: <a className="contact-github" href="https://github.com/xodnrdlwy">https://github.com/xodnrdlwy</a></li>
                    </ul>
                </div>
            </article>
        </div>
    </section>
);

export function Part1() {
    const [showIntroduction, setShowIntroduction] = useState(true);

    const handleToggle = () => {
        setShowIntroduction(!showIntroduction);
    };

    return (
        <>
            <div className='first-card-container'>
                <Card
                    className='About-first-card'
                    cover={<img alt="프로필사진" src={Profile} />}
                    bodyStyle={{ padding: 0 }}
                />
                <Divider />
                <div className='About-name-container'>
                    <label className='About-name'>
                        Kim TaeWook
                    </label>
                </div>
            </div>
                {showIntroduction ? <IntroductionSection onToggle={handleToggle} /> : <AboutMeSection onToggle={handleToggle} />}


        </>
    );
}
